.services_container {
    grid-template-columns: repeat(3, 1fr);
    column-gap: 1.875rem;
}

.services_card{
    text-align: center;
    padding: 1.875rem;
    border-radius: var(--border-radius);
    background-color: var(--container-color);
    transition: all 0.3s ease-in-out;
}

.services_card:hover{
    transform: translateY(-10px);
}

.services_card:nth-child(1){
    background-color: rgb(108, 108, 229);
    box-shadow: 0px 5px 15px 0px rgb(108, 108, 229);
}

.services_card:nth-child(2){
    background-color: rgb(249, 215, 76);
    box-shadow: 0px 5px 15px 0px rgb(249, 215, 76);
}

.services_card:nth-child(3){
    background-color: rgb(249, 123, 139);
    box-shadow: 0px 5px 15px 0px rgb(249, 123, 139);
}

.services_img {
    margin-bottom: 1.25rem;
}

.services_title{
    font-size: var(--h3-font-size);
    margin-bottom: 1rem;
    color: #fff;
}

.services_description {
    color: #fff;
}

.services_card:nth-child(2) .services_title {
    color: rgb(147, 118, 0);
}

.services_card:nth-child(2) .services_description {
    color: rgb(147, 118, 0);
}


@media screen and (max-width: 1024px) {
    .services_container {
        grid-template-columns: repeat(2, 330px);
        justify-content: center;
        row-gap: 1.875rem;

    }
}


@media screen and (max-width: 768px) {
    .services_container {
        grid-template-columns: 330px ;
    }
}


@media screen and (max-width: 350px) {
    .services_container {
        grid-template-columns: 1fr ;
    }
}